import { Helmet } from "react-helmet-async";
import { Link } from "react-router-dom";
import styled from "styled-components";

const NotFound = styled.div`
  height: calc(100vh - 300px);
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  line-height: 30px;
  padding-top: 80px;
  h2 {
    font-size: 60px;
    font-weight: 700;
    margin: 60px 0;
  }
  h1 {
    width: 180px;
    height: 40px;
    border-radius: 14px;
    background-color: #1d1d1d;
    font-size: 16px;
    font-weight: 500;
    margin-top: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  h1:hover {
    background-color: #00a7f6;
  }
  h4 {
    max-width: 440px;
    width: 100%;
    text-align: center;
  }

  @media screen and (max-width: 480px) {
    h1 {
      width: 160px;
      font-size: 14px;
    }
    h2 {
      font-size: 50px;
      font-weight: 500;
      margin: 60px 0;
    }
    h4 {
      max-width: 380px;
      width: 100%;
      text-align: center;
    }
  }
`;

export const Error404 = () => {
  return (
    <div>
      <NotFound>
        <Helmet>
          <title>Error 404</title>
        </Helmet>
        <h2>404</h2>
        <h4>
              .     .
              .
        </h4>
        <Link to={"/"}>
          <h1> </h1>
        </Link>
      </NotFound>
    </div>
  );
};

