import { Button, Errors, Form, Wrap } from "./Login";
import { Helmet } from "react-helmet-async";
import { useForm } from "react-hook-form";
import { useNavigate } from "react-router-dom";

export const Join = () => {
  const {
    register,
    handleSubmit,
    formState: { errors, isValid },
  } = useForm();
  const nav = useNavigate();

  const submitHandler = () => {
    if (isValid === true) {
      nav("/", {
        replace: true,
      });
    }
  };
  const ClickHandler = () => {
    nav("/login");
  };
  return (
    <Wrap>
      <Helmet>
        <title></title>
      </Helmet>
      <Form onSubmit={handleSubmit(submitHandler)}>
        <h1>PinMovie</h1>
        <input
          {...register("username", {
            required: " .",
            minLength: {
              message: " 2 ",
              value: 2,
            },
          })}
          type=""
          placeholder=" ."
        ></input>
        <Errors>{errors?.username?.message}</Errors>
        <input
          {...register("password", {
            required: " .",
            minLength: {
              value: 8,
              message: " 8 ",
            },
          })}
          type="password"
          placeholder=" ."
        ></input>
        <Errors>{errors?.password?.message}</Errors>
        <input
          {...register("password", {
            required: " .",
            minLength: {
              value: 12,
              message: " 12 ",
            },
          })}
          type="password"
          placeholder=" ."
        ></input>
        <Errors>{errors?.password?.message}</Errors>

        <Button $bgcolor={isValid} onClick={ClickHandler}>
          
        </Button>
      </Form>
    </Wrap>
  );
};
