import { Helmet } from "react-helmet-async";
import { useForm } from "react-hook-form";
import { useNavigate } from "react-router-dom";
import styled from "styled-components";

export const Wrap = styled.div`
  padding-top: 120px;
  display: flex;
  justify-content: center;
`;
export const Form = styled.form`
  width: 450px;
  height: 700px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;

  h1 {
    font-size: 40px;
    font-weight: 900;
    padding-bottom: 80px;
  }
  input {
    all: unset;
    width: 80%;
    height: 45px;
    padding-left: 20px;
    background-color: white;
    margin-top: 20px;
    border-radius: 20px;
    color: black;
  }
`;
export const Button = styled.button`
  all: unset;
  width: 85%;
  height: 45px;
  text-align: center;
  background-color: ${(props) => (props.$bgcolor ? "#007aff" : "#1c1c1c")};
  margin-top: 70px;
  border-radius: 20px;
`;
export const Errors = styled.div`
  width: 80%;
  padding-left: 10px;
  margin-top: 5px;
  color: #f95a5a;
`;
export const Join = styled.div`
  width: 85%;
  display: flex;

  padding-top: 10px;
  p {
    padding-left: 20px;
  }
`;
const NavJoin = styled.div`
  padding-left: 20px;
`;
////////////////////////////////////////////////////////////////////////
export const Login = () => {
  const {
    register,
    handleSubmit,
    formState: { errors, isValid },
  } = useForm();
  const nav = useNavigate();

  const submitHandler = () => {
    if (isValid === true) {
      nav("/", {
        replace: true,
      });
    }
  };
  const ClickHandler = () => {
    nav("/join");
  };
  return (
    <Wrap>
      <Helmet>
        <title></title>
      </Helmet>
      <Form onSubmit={handleSubmit(submitHandler)}>
        <h1>PinMovie</h1>
        <input
          {...register("username", {
            required: " .",
            minLength: {
              message: " 2 ",
              value: 2,
            },
          })}
          type=""
          placeholder=" ."
        ></input>
        <Errors>{errors?.username?.message}</Errors>
        <input
          {...register("password", {
            required: " .",
            minLength: {
              value: 8,
              message: " 8 ",
            },
          })}
          type="password"
          placeholder=" ."
        ></input>
        <Errors>{errors?.password?.message}</Errors>
        <Join>
          <p>  ?</p>
          <NavJoin onClick={ClickHandler}></NavJoin>
        </Join>
        <Button $bgcolor={isValid}></Button>
      </Form>
    </Wrap>
  );
};
